/* =============================================
reset
============================================= */

html {
  background-color: #0f0f12;
}

html::-webkit-scrollbar,
div::-webkit-scrollbar {
  display: none;
}

p,
a,
button,
span {
  font-size: 14px;
}

h1 {
  font-size: 36px;
  font-weight: 700;
}

h2 {
  font-size: 32px;
  font-weight: 700;
}

h3 {
  font-size: 24px;
  font-weight: 700;
}

h4 {
  font-size: 20px;
  font-weight: 500;
}

h5 {
  font-size: 14px;
  font-weight: 400;
}

h6 {
  font-size: 13px;
  font-weight: 400;
}

p,
a,
button,
span,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #fff;
  margin: 0;
}

.fw100 {
  font-weight: 100;
}

.fw200 {
  font-weight: 200;
}

.fw300 {
  font-weight: 300;
}

.fw400 {
  font-weight: 400;
}

.fw500 {
  font-weight: 500;
}

.fw600 {
  font-weight: 600;
}

.fw700 {
  font-weight: 700;
}

.fw800 {
  font-weight: 800;
}

.fw900 {
  font-weight: 900;
}

.mt5 {
  margin-top: 5px;
}

.mt10 {
  margin-top: 10px;
}

.mt15 {
  margin-top: 15px;
}

.mt20 {
  margin-top: 20px;
}

.mt25 {
  margin-top: 25px;
}

.mt30 {
  margin-top: 30px;
}

.mt35 {
  margin-top: 35px;
}

.mt40 {
  margin-top: 40px;
}

.mt45 {
  margin-top: 45px;
}

.mt50 {
  margin-top: 50px;
}

.mt55 {
  margin-top: 55px;
}

.mt60 {
  margin-top: 60px;
}

a,
a:link,
a:visited,
a:active,
a:hover {
  text-decoration: none;
  color: #222;
  cursor: pointer;
}

a,
input,
button {
  -webkit-tap-highlight-color: transparent;
}

ul {
  list-style: none;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0;
}

button {
  padding:0;
  margin:0;
  background:none;
  border:none;
  cursor: pointer;
}

.btn-filter {
  height:30px;
  padding:0 10px;
  border:1px solid #fff;
  border-radius:6px;
}

button:hover {
  background-color: none;
}

input:focus {
  outline: none;
}

div,
header,
input {
  box-sizing: border-box;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  ;

  100% {
    opacity: 100%;
  }
}

/* =============================================
App
============================================= */

.contents {
  margin: 80px 0;
  padding: 0 20px;
  background-color: #0f0f12;
  animation: fadein .1s;
}

.contents ul li.section:nth-child(n+2) {
  margin-top: 30px;
}

/* =============================================
Header
============================================= */

.App-header {
  width: 100%;
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #0f0f12;
  position: fixed;
  top: 0;
}

.logo div {
  width: 80px;
  height: 40px;
  background: url(../src/assets/logo.svg) center no-repeat;
  background-size: 80px;
}

.nav {
  display: flex;
}

.slide-menu {
  width: 200px;
  height: 200vh;
  background-color: #2e5bff;
  position: absolute;
  right: -200px;
  transition: .3s;
}

/* =============================================
home
============================================= */

.nav-con-bottom {
  width: 100%;
  height: 60px;
  background-color: #18161c;
  border-top: 1px solid #333;
  border-radius: 30px 30px 0 0;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

.nav-con-bottom a {
  width: 50%;
  height: 60px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.nav-con-bottom a .btn-tab {
  display: none;
  width: 20px;
  height: 20px;
  color: black;
}

.nav-con-bottom .link-tab .btn-tab.on,
.nav-con-bottom .link-tab.on .btn-tab {
  display: none;
}

.nav-con-bottom .link-tab .btn-tab,
.nav-con-bottom .link-tab.on .btn-tab.on {
  display: block;
}

.nav-con-bottom a p {
  font-size: 11px;
  color: #777;
  margin-top: 6px;
  text-align: center;
  position: relative;
}

.nav-con-bottom a.on p {
  color: #fff;
  font-weight: 700;
}

.main-con ul .main-box01 {
  display: flex;
  flex-wrap: wrap;
  background-color: #0f0f12;
  padding: 20px;
  margin: -20px -20px 0;
}

.main-con ul .main-box01 a:nth-child(2n) {
  margin-left: 10px;
}

.main-con ul .main-box01 a:nth-child(n+3) {
  margin-top: 10px;
}

.main-con ul .main-box01 a {
  position: relative;
  width: calc((100% - 10px) * .5);
  height: calc((100vw - 50px) * .4);
  padding: 20px;
  background-color: #18161c;
  border-radius: 10px;
  box-sizing: border-box;
}

.main-con ul .main-box01 a .banner .txt h3 {
  color: #fff;
}

.main-con ul .main-box01 a .banner .simb-area {
  position: absolute;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  border-radius: 30%;
  background-color: #fff;
  opacity: .1;
}

.main-con ul .swiper-box01 .mySwiper {
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
}

.main-con ul .swiper-box01 .mySwiper .slide {
  width: 100%;
  aspect-ratio: 1.2 / 1;
  border-radius: 10px;
  margin-top: 10px;
  height: auto;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide01 {
  background: url(../src/assets/img-swiper_01.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide02 {
  background: url(../src/assets/img-swiper_02.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide03 {
  background: url(../src/assets/img-swiper_03.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide04 {
  background: url(../src/assets/img-swiper_04.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide05 {
  background: url(../src/assets/img-swiper_05.jpg) center no-repeat;
  background-size: cover;
}

.main-con ul .swiper-box01 .mySwiper .slide.slide06 {
  background: url(../src/assets/img-swiper_06.jpg) center no-repeat;
  background-size: cover;
}

/* =============================================
apart
============================================= */

.Apart .apart-con ul .section.list-box .filter-area {
  padding:20px 0;
  margin:-20px 0 20px;
  border-bottom:1px solid #333;
}

.Apart .apart-con ul .section.list-box .filter-area .btn-filter {
  display:flex;
  align-items:center;
}

.Apart .apart-con ul .section.list-box .filter-area .btn-filter .arrow {
  width:16px;
  height:16px;
  margin-left:8px;
  background:url('../src/assets/btn-arrow.svg') center no-repeat;
  background-size:16px;
}

.Apart .apart-con ul .section.list-box .list-area .list-group a {
  display: flex;
}

.Apart .apart-con ul .section.list-box .list-area .list-group a .img-thumb {
  width: calc(38%);
  height: calc((100vw - 40px) * .38 * .8);
  background-color: #fff;
  border-radius: 6%;
  text-align: center;
  overflow: hidden;
}

.Apart .apart-con ul .section.list-box .list-area .list-group a .img-thumb img {
  display: inline;
  width: auto;
  height: 100%;
}

.Apart .apart-con ul .section.list-box .list-area .list-group a .info-area {
  width: calc(58%);
  margin-left: 4%;
}

.Apart .apart-con ul .section.list-box .list-area .list-group ~ .list-group {
  border-top: 1px solid #333;
  padding-top: 20px;
  margin-top: 20px;
}


/* =============================================
mypage
============================================= */

.MyPage .set-con {
  width: 100%;
  margin: 0 -20px;
}

.MyPage .set-con ul li {
  width: calc(100% + 40px);
  padding: 20px 20px;
  box-sizing: border-box;
}

.MyPage .set-con ul li + li {
  border-top: 1px solid #eee;
}


/* =============================================
popup bottom
============================================= */

.pop-bottom {
  display:none;
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:200vh;
  z-index:500;
}

.pop-bottom .pop-bottom-area {
  position:fixed;
  bottom:0;
  width:100%;
  max-height:400px;
  background-color: #18161c;
  border-top: 1px solid #333;
  border-radius: 30px 30px 0 0;
  overflow:hidden;
}

.pop-bottom .pop-bottom-area .tit-area {
  display:flex;
  align-items:center;
  justify-content:space-between;
  width:100%;
  height:70px;
  padding:0 20px;
}

.pop-bottom .pop-bottom-area .tit-area .btn-close div {
  width:16px;
  height:16px;
  background:url('../src/assets/btn-close.svg') center no-repeat;
  background-size:16px;
}

.pop-bottom .pop-bottom-area .contents-area {
  width:100%;
  padding:0 20px 40px;
  height:360px;
  overflow:scroll;
}

.pop-bottom .pop-bottom-area .contents-area .data {
  list-style:none;
}

.pop-bottom .pop-bottom-area .contents-area .data.on {
  padding:0 20px;
  margin:0 -20px;
  background-color:#2e5bff;
}

.pop-bottom .pop-bottom-area .contents-area .data ~ .data {
  border-top: 1px solid #333;
}

.pop-bottom .pop-bottom-area .contents-area .data button {
  width:100%;
  text-align:left;
}

.pop-bottom .pop-bottom-area .contents-area .data button h5 {
  padding:20px 0;
}

.pop-bottom .dimmed {
  position:absolute;
  width:100%;
  height:200vh;
  background-color:#000;
  opacity:.5;
}